import React from 'react';
import './App.css';
import { BrowserRouter as Router, Routes, Route} from "react-router-dom"
import Home from './pages';


function App() {
  return (

    <Router basename={window.location.pathname || ''}>
      <Routes>
        <Route path='/' element={<Home />} />
     </Routes>
   </Router>
  );
}

export default App;
